@font-face {
    font-family: StratosSkyeng;
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: url('./assets/fonts/stratosskyengweb-regular.woff') format('woff');
}

body {
    margin: 0;
    padding: 0;
    background: var(--blue-120, #004980);
    font-family: StratosSkyeng, sans-serif;
}

.header {
    min-height: 155px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-left: 23px;
    margin-right: 22px;
}

.timer {
    width: 155px;
    height: 91px;
    color: #fff;
}

.timer-text {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    gap: 57px;
    font-size: 16px;
    padding-right: 5px;
}

.timer-count {
    font-size: 64px;
    line-height: 72px;
}

.container {
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.info {
    width: 480px;
    height: 459px;
    border-radius: 12px;
    background: #c2f5ff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 158px 272px;
    padding-left: 70px;
    padding-right: 70px;
}

.info-img {
    margin-top: 28px;
}

.title {
    text-align: center;
    color: #004980;
    font-size: 40px;
    line-height: 48px;
    margin-block-start: 0;
    margin-block-end: 0;
}

.form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.form-level {
    display: flex;
    gap: 26px;
    margin-top: 48px;
    margin-bottom: 67px;
}

input {
    display: none;
}

input[type='radio']:checked + label {
    border: 1px solid #000;
}

.form-level-choice {
    width: 97px;
    height: 98px;
    background: #fff;
    border-radius: 12px;
    color: var(--blue-100, #0080c1);
    text-align: center;
    font-size: 64px;
    line-height: 72px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.button {
    width: 246px;
    height: 50px;
    border-radius: 12px;
    background: var(--salad-60, #7ac100);
    color: #fff;
    text-align: center;
    font-size: 24px;
    line-height: 32px;
    border-width: 0;
}

.game-field {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(95px, 1fr));
    grid-gap: 15px;
    margin-bottom: 49px;
    margin-left: 26px;
    margin-right: 23px;
}

.card {
    width: 95px;
    height: 133px;
    border-radius: 4px;
}

.card-front,
.card-back {
    width: 100%;
    height: 100%;
}

.card-front {
    background-color: #fff;
    border-radius: 4px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.card-top {
    height: 55px;
    display: flex;
    flex-direction: column;
    margin: 0 6px;
}

.card-top-flipped {
    rotate: 180deg;
}

.card-title {
    font-size: 24px;
    line-height: 32px;
}

.card-suites {
    display: flex;
    justify-content: center;
}

.card-suites img {
    width: 29px;
    height: 25px;
}

.card-suites-small {
    width: 7px;
    height: 6px;
    margin-left: 3px;
}

.result-title {
    margin-top: 8px;
    margin-bottom: 28px;
}

.result-title-time {
    font-size: 24px;
    line-height: 32px;
    margin: 0 0 10px;
}

.result-count {
    color: #000;
}

.result-go-button {
    margin-top: 38px;
    margin-bottom: 50px;
}

dialog {
    width: 480px;
    height: 459px;
    border-radius: 12px;
    background: #c2f5ff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;
    border: none;
}

dialog::backdrop {
    background-color: rgb(0 73 128 / 90%);
}
